import { Clue } from "./clue";
import { Row, RowState } from "./Row";
import { maxGuesses } from "./util";

export function About() {
  return (
    <div className="App-about">
      <p>
        <i>Waadoru</i> is a Japanese adaptation of a word-guessing game called <a href="https://www.powerlanguage.co.uk/wordle/">
          <i>Wordle</i>
        </a>.
      </p>
      <p>
        You must guess a target word in {maxGuesses} tries or less.
        <br />
        With each try, you will get some hints about that target word.
      </p>
      <hr />
      <p>
        <h2>The Japanese Kana Table</h2>
        <table className="kana-table">
          <th colSpan={17}>
            <b className="green-black-bg"></b> Columns
          </th>
          <th></th>
          <tr>
            <th rowSpan={6} className="border-none"></th>
            <th rowSpan={5} className="border-none"></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th rowSpan={7}>
              
              Rows
              
              <br />
              <b className="black-green-bg"></b>
            </th>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <th></th>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <th></th>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <th></th>
          </tr>
          <tr>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <td></td>
            <th></th>
          </tr>
          <tr>
            <th></th>
            <td colSpan={15} className="border-none"></td>
          </tr>
          <tr>
            <th></th>
            <td colSpan={16} className="border-none"></td>
          </tr>
        </table>
      </p>
      <p>
        Each column corresponds to a particular consonant.
        For example the "K" column with.
        Note that the diacritics and (e.g.,  or ) are in the same column as the kana they are based on (e.g., ),
        but they are considered separate characters for the game.
      </p>
      <p>
        Each row corresponds to a particular vowel.
        For example the "I" row with.
      </p>
      <p>
        andare considered separate from all the rows and columns.
      </p>
      <hr />
      <h2>Rules</h2>
      <p>
        <b className="green-bg"></b> emoji means a kana matched with the target word.
      </p>
      <p>
        <b className="purple-bg"></b>
         emoji means that the diacritic (e.g. //) or the size (e.g. /) is incorrect.
      </p>
      <p>
        <b className="gray-bg"></b>
         emoji means that the kana is not found at all in the target word.
      </p>
      <p>
        <b className="green-gray-bg"></b>
         emoji means that the column is correct.
      </p>
      <p>
        <b className="gray-green-bg"></b>
         emoji means that the row is correct.
      </p>
      <p>
        <b className="yellow-bg"></b>
         emoji means that the kana is present somewhere else in the target word.
      </p>
      <p>
        <b className="green-yellow-bg"></b>
         emojimeans the combination of both <b className="yellow-bg"></b> and 
        <b className="green-black-bg"></b>.
      </p>
      <p>
        <b className="yellow-green-bg"></b>
         emoji means the combination of both <b className="yellow-bg"></b> and
        <b className="black-green-bg"></b>.
      </p>
      <hr />
      <Row
          romanjiMode={false}
        rowState={RowState.LockedIn}
        wordLength={7}
        cluedLetters={[
          { clue: Clue.Almost, letter: "" },
          { clue: Clue.CorrectConsonant, letter: "" },
          { clue: Clue.Correct, letter: "" },
          { clue: Clue.CorrectVowelAndElsewhere, letter: "" },
          { clue: Clue.Absent, letter: "" },
          { clue: Clue.CorrectVowel, letter: "" },
          { clue: Clue.Elsewhere, letter: "" },
        ]}
      />
      <p>
        <b></b> is not in the target word at all.
      </p>
      <p>
        <b className="green-bg"></b> is correct! The 3rd kana is <b className="green-bg"></b>.
        <br />
        <strong>
          (but beware,may also be somewhere else!)
        </strong>
      </p>
      <p>
        <b className="purple-bg"></b> is almost correct.
        <br />
        The diacritic or size is not good, so the correct kana has to be .
      </p>
      <p>
        <b className="yellow-bg"></b> is somewhere else in the target word.
        <br />
        <strong>(at least once, but could be more of them </strong>
      </p>
      <p>
        <b className="gray-green-bg"></b> means both <b className="gray-bg"></b> and <b className="black-green-bg"></b>.
        <br />
        <b className="black-green-bg"></b> means that the correct kana is in the same line.
        <br />
        In other words, there is nohere or anywhere else in the target word,
        <br />
        and that next-to-last kana contains the vowel "A".
      </p>
      <p>
        <b className="green-gray-bg"></b> means both <b className="gray-bg"></b> and <b className="green-black-bg"></b>.
        <br />
        <b className="green-black-bg"></b> means that the correct kana is in the same column.
        <br />
        In other words, there is nohere or anywhere else in the target word,
        <br />
        and that second kana contains the consonant "Y" (, )
      </p>
      <p>
        <b className="green-yellow-bg"></b> means both <b className="yellow-bg"></b> and <b className="green-black-bg"></b>.
        <br />
        In others words, there is at least onesomewhere else in the target word,
        <br />
        and that 4th kana contains the consonant "T" (, ...)
      </p>
      <hr />
      <p>
        If you have a question about the rules or my translation, please open a ticket <a href="https://github.com/itsupera/waadoru/issues">here</a>.
        For a technical issue ask skytomo221 (the original author) <a href="https://github.com/skytomo221/waadoru/issues">here</a>
      </p>
      <p>
        If you like the game, please <a href="https://ko-fi.com/skytomo">donate to skytomo</a> (not me!).
        <br/>
        He is the author of the Japanese adaptation and deserve the credits :)
        <br/>
        <br/>
        I (<a href="https://github.com/itsupera">itsupera</a>) only made so minor tweaks and translated to English.
      </p>
    </div>
  );
}
